import React from 'react'
import Slider from 'react-slick'
import AshlynLogo from '../../Images/Logos/Ashlyn-So-logo.png'
import DeWittLogo from '../../Images/Logos/de-witt.webp'
import HairDecorLogo from '../../Images/Logos/hair-decor-logo.png'
import KeepfaceLogo from '../../Images/Logos/keepface-logo.png'
import MarineToysLogo from '../../Images/Logos/marine-toys-for-tots-bear-with-logo.jpg'
import NYUSternLogo from '../../Images/Logos/nyu-stern-vector-logo.svg'
import ScarolaLogo from '../../Images/Logos/scarola-photography-logo.webp'
import ShamansLogo from '../../Images/Logos/shamans-logo-ai.webp'

function Carousel() {

  const settings = {
      accessibility: false,
      centerMode: false,
      cssEase: "linear",
      focusOnSelect: false,
      draggable: false,
      autoplay: true,
      autoplaySpeed: 0,
      arrows: false,
      pauseOnHover: true,
      dots: false,
      infinite: true,
      speed: 3000,
      slidesToShow: 5,
      slidesToScroll: 1,
      responsive: [{
        breakpoint: 786,
          settings: {
            slidesToShow: 4,
            centerMode: true
          }
      }, {
        breakpoint: 520,
        settings: {
          slidesToShow: 3,
          centerMode: true
        }
      }]
  }

  return (
    <div className="px-72">
      <Slider {...settings}>
        <div className="">
          <img
          src={AshlynLogo}
          alt="ashlyn-logo"
          className="w-3/6 items-center"
          />
        </div>
        <div className="">
          <img
          src={DeWittLogo}
          alt="dewitt-logo"
          className="w-3/5"
          />
        </div>
        <div className="">
          <img
          src={HairDecorLogo}
          alt="hairdecor-logo"
          className="w-3/5"
          />
        </div>
        <div>
          <img
          src={KeepfaceLogo}
          alt="keepface-logo"
          className="w-3/5"
          />
        </div>
        <div>
          <img
          src={MarineToysLogo}
          alt="marinetoys-logo"
          className="w-3/5"
          />
        </div>
        <div>
          <img
          src={NYUSternLogo}
          alt="nyu-logo"
          className="w-3/5"/>
        </div>
        <div>
          <img
          src={ScarolaLogo}
          alt="scarola-logo"
          className="w-3/5"
          />
        </div>
        <div>
          <img
          src={ShamansLogo}
          alt="shamans-logo"
          className="w-3/5"
          />
        </div>
      </Slider>
    </div>
  )
}

export default Carousel;
