import React from 'react'
import YellowButton from '../HeaderComponents/YellowButton'
import aboutImage from '../../Images/about-image.png'

function FeatureAbout() {
  return (
  <div>
    <div className="flex justify-center pb-6">
      <h1 className="font-reenie text-4xl ml-80">
        I know exactly what it is like to go through cancer treatment.
      </h1>
    </div>
    <div className="flex px-56 w-full">
      <div className="w-3/12 flex justify-center transform scale-100">
        <img className="rounded-xl" src={aboutImage} alt="about-elana"/>
      </div>
      <div className="w-9/12 px-11">
        <p className="pb-5">I know what it feels like to be sinking into a deep dark hole of needles, medicine, and surgeries. I also know that there are side effects of these treatments that children must deal with for the rest of their lives. I hate it when children stare at my scars with looks of pity. I survived, but the scars on my body and my heart will never go away.</p>
        <p className="pb-5">On my last day of chemo, I was the happiest person in the world. But when I looked around the hospital at every kid who was suffering, I realized that other children are going through the torture that had just ended for me. I decided to start my foundation—Koenig Childhood Cancer Foundation—with my sister and family, and I have been giving back and helping other children diagnosed with cancer so that they can be spared the pain that I felt. I show them emotional support and financial help so that they can fight together with their parents, and so the children can stand up for themselves. I also spread awareness about childhood cancer by singing and making speeches.</p>
        <p>To keep helping kids and their families, and to make my foundation strong, I need your help!</p>
        </div>
      </div>
      <div className="w-full flex justify-between py-6 px-56">
        <YellowButton
        text="Learn More"
        className="relative left-80 bg-orange-400 rounded-full px-8 py-1.5 text-xl font-bold"/>
        <p className="font-reenie text-3xl">by Elana Koenig, Founder</p>
      </div>
    </div>
  )
}

export default FeatureAbout;
