import React from 'react'
import { Link } from 'react-router-dom'
import CloseOpenGetInvolvedMenu from './CloseOpenGetInvolvedMenu'

function HeaderMenuItems(props) {

  return (
    <div>
    <ul className="font-nunito text-sm flex px-5 uppercase font-bold">

      <li className={props.about}><Link to="/about">About</Link></li>

      <CloseOpenGetInvolvedMenu />

      <li className={props.news}><Link to="/news">News</Link></li>

    </ul>
    </div>
  )
}

export default HeaderMenuItems;
