import React from 'react'

function BlogArticle(props) {
  return (
    <div className="w-1/5 mx-3 py-16">
      <div>
        <img className="rounded-t-lg" src={props.url} alt={props.alt}/>
      </div>
      <div className="rounded-b-lg bg-gray-50 px-3 py-2">
        <p className="text-gray-400 text-sm">{props.date}</p>
        <h1 className="font-bold text-sm pt-2 pb-5">{props.title}</h1>
        <p className="text-sm">{props.content}</p>
      <div>
        <p className="flex text-purple-300 font-bold justify-end text-sm py-2 pr-4">Read More</p>
      </div>
      </div>
    </div>
  )
}

export default BlogArticle;
