import React, { useState } from 'react'
import GetInvolvedMenu from './GetInvolvedMenu'
import PurpleStar from './PurpleStar'
import { useTransition, animated} from 'react-spring'

function CloseOpenGetInvolvedMenu() {

  const [showMenuStar, setShowMenuStar] = useState(false);

  const transitions = useTransition(showMenuStar, null, {
  from: { position: 'absolute', opacity: 0 },
  enter: { opacity: 1 },
  leave: { opacity: 0 },
  })

  const starTransitions = useTransition(showMenuStar, null, {
  from: { position: 'absolute', opacity: 0 },
  enter: { opacity: 1 },
  leave: { opacity: 0 },
  })

  return (
  <div>
    {
      starTransitions.map(({ item, key, props }) =>
      item &&
      <animated.div
      key={key}
      style={props}
      >
    <PurpleStar
    closeSubMenu={() => setShowMenuStar(false)}/>
    </animated.div>
    )
    }
    <li
    className="mx-4 mb-2 cursor-pointer"
    onClick={() => setShowMenuStar(!showMenuStar)}
    >Get Involved</li>
    {
      transitions.map(({ item, key, props }) =>
  item &&
  <animated.div
  key={key}
  style={props}
  >
  <GetInvolvedMenu
  closeSubMenu={() => setShowMenuStar(false)}/>
  </animated.div>
  )
  }
  </div>
  )
}

export default CloseOpenGetInvolvedMenu;
