import React from 'react'
import { Link } from 'react-router-dom'

function FooterMenu(props) {
  return (
    <div className={props.className}>
      <ul className="font-nunito text-sm flex px-5 uppercase font-bold">

        <li className={props.about}><Link to="/about">About</Link></li>

        <li className={props.getInvolved}><Link to="/">Get Involved</Link></li>

        <li className={props.news}><Link to="/news">News</Link></li>

      </ul>
    </div>
  )
}

export default FooterMenu;
