import React from 'react'
import Logo from '../../Images/kccflogo.png'
import HeaderSocial from '../HeaderComponents/HeaderSocial'
import YellowButton from '../HeaderComponents/YellowButton'
import FooterMenu from './FooterMenu'

function Footer() {
  return (
    <div className="">
      <div className="pt-8 pb-3 mx-40 border-dotted border-t-3 border-gray-400">
        <div className="flex justify-between items-center">
            <img className="w-3/12" src={Logo} alt="kccf-logo"/>
            <div className="flex items-center">
              <div className="pr-12">
                <HeaderSocial />
              </div>
                <YellowButton
              text="Donate"
              className="bg-orange-400 rounded-2xl px-14 py-1.5 text-2xl font-bold"/>
            </div>
          </div>
          <div className="flex justify-between">
            <p className="ml-3 mt-2 text-xs font-bold">The Koenig Childhood Cancer Foundation is a registered 503(c)(3) nonprofit.</p>
            <div className="mr-20">
              <FooterMenu
              className="flex items-center"
              getInvolved="px-3 border-black border-r-3"
              about="pr-3 border-black border-r-3"
              news="pl-3"
              />
            </div>
          </div>

      </div>
    </div>
  )
}

export default Footer;
