import React from 'react'
import { Link } from 'react-router-dom'

function GetInvolvedMenu(props) {
  return (
    <div className="font-nunito w-20 items-center absolute left-8 border-t border-black">
      <ul className="mt-1 capitalize font-thin absolute left-2 text-center">
        <li className=""><Link
        to="/volunteers"
        className="block"
        onClick={props.closeSubMenu}
        >Volunteers</Link></li>

        <li><Link to="/Caregivers"
        className="block"
        onClick={props.closeSubMenu}
        >Caregivers</Link></li>

        <li><Link to="/partners"
        className="block"
        onClick={props.closeSubMenu}
        >Partners</Link></li>

        <li><Link to="/contact"
        className="block"
        onClick={props.closeSubMenu}
        >Contact</Link></li>
      </ul>
    </div>
  )
}

export default GetInvolvedMenu;
