import React from 'react'
import FeatureText from '../Components/FeatureComponents/FeatureText'
import HeroPicture from '../Components/FeatureComponents/HeroPicture'
import MissionText from '../Components/FeatureComponents/MissionText'
import FeatureAbout from '../Components/FeatureComponents/FeatureAbout'
import Testimonials from '../Components/FeatureComponents/Testimonials'
import ImpactCards from '../Components/FeatureCardsComponents/ImpactCards'
import Carousel from '../Components/CarouselComponents/Carousel'
import HeadCarousel from '../Components/CarouselComponents/HeadCarousel'
import Footer from '../Components/FooterComponents/Footer'
import HeadNews from '../Components/NewsComponents/HeadNews'
import BlogArticles from '../Components/NewsComponents/BlogArticles'

function Home() {
  return (
    <div>
    <section id="feature" className="">
      <div className="flex overflow-hidden">

        <FeatureText />

        <HeroPicture />

      </div>

        <MissionText />
    </section>

    <section id="feature-about" className="py-10">
      <FeatureAbout />
    </section>

    <section id="testimonials" className="py-20">
      <Testimonials />
    </section>

    <section id="impactcards" className="py-20">
      <ImpactCards />
    </section>

    <section id="partners" className="">
      <HeadCarousel />
      <Carousel />
    </section>

    <section id="news" className="py-20">
      <HeadNews />
      <BlogArticles />
    </section>

    <section id="footer" className="">
      <Footer />
    </section>

    </div>
  )
}

export default Home;
