import React from 'react'
import WhiteStar from '../../Images/white_star.png'

function MissionText() {
  return (
    <div className="flex-col justify-center px-20 relative bottom-24">
      <div className="bg-purple-500 rounded-2xl px-10 pt-6 pb-3 text-white">
        <h1 className="flex justify-center uppercase text-xl pb-2">The Mission of KCCF is:</h1>
        <p className="m-auto font-bold text-xl w-11/12">To build the child-led <span className="text-orange-400">community of healing</span> that individualizes support to families fighting childhood cancer.
        We're there from diagnosis to beyond remission, beacuse the financial and emotional burden of illness lingers even if the cancer itself does not.</p>
        <div className="flex justify-center pt-2">
        <img src={WhiteStar} alt="white-star"/>
        </div>
      </div>
    </div>
  )
}

export default MissionText;
