import React from 'react'

function ImpactCard(props) {
  return (
    <div className="bg-gray-200 w-1/6 rounded-lg mx-2 text-center">
      <div className="bg-purple-500 text-lg uppercase rounded-t-lg py-1 text-white">
        <h1>{props.headtext}</h1>
      </div>
      <div className="w-4/5 m-auto py-3">
        <p className="text-sm">
          {props.textOne}
        </p>
        <p className="text-4xl font-bold py-1">
          {props.number}
        </p>
        <p className="text-sm">
          {props.textTwo}
        </p>
      </div>
    </div>
  )
}

export default ImpactCard;
