import React from 'react'
import testimonialOne from '../../Images/circle-profile1.png'
import testimonialTwo from '../../Images/circle-profile2.png'
import testimonialThree from '../../Images/circle-profile3.png'
import testimonialFour from '../../Images/circle-profile4.png'
import Testimonial from './Testimonial'


function Testimonials() {
  return (
    <div className="flex relative px-56">
      <Testimonial
      circle1="-left-12 -top-1 absolute rounded-full w-40 h-40 bg-purple-400"
      circle2="left-40 -bottom-1 absolute rounded-full w-20 h-20 bg-purple-600"
      picture={testimonialOne}/>

      <Testimonial
      circle1="left-28 -top-4 absolute rounded-full w-40 h-40 bg-purple-300"
      circle2=""
      picture={testimonialTwo}
      imgClass="object-cover w-full h-64"/>

      <Testimonial
      circle1="-bottom-5 absolute rounded-full w-40 h-40 bg-orange-400"
      circle2="left-40 -top-1 absolute rounded-full w-20 h-20 bg-orange-400"
      picture={testimonialThree}
      imgClass="object-cover h-64 w-full"/>

      <Testimonial
      circle1="right-1 -bottom-0 absolute rounded-full w-40 h-40 bg-purple-400"
      circle2=""
      picture={testimonialFour}
      imgClass="object-cover h-64 w-full"/>
    </div>
  )
}

export default Testimonials;
