import React from 'react'
import logo from '../../Images/kccflogo.png'
import HeaderSocial from './HeaderSocial'
import { Link } from 'react-router-dom'
import YellowButton from './YellowButton'

function Header() {
  return (
    <header className="">
    <div className="flex justify-between pt-2 px-20">

      <HeaderSocial />
      <div className="w-1/5">
        <Link to="/"><img src={logo} alt="KccfLogo"/></Link>
      </div>

      <YellowButton
        text="Donate"
        className="bg-orange-400 rounded-2xl px-14 py-1.5 text-2xl font-bold"/>

    </div>
  </header>
  )
}

export default Header;
