import React from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faLinkedin } from '@fortawesome/free-brands-svg-icons'
import { faInstagramSquare } from '@fortawesome/free-brands-svg-icons'
import { faFacebookSquare } from '@fortawesome/free-brands-svg-icons'
import { faTwitterSquare } from '@fortawesome/free-brands-svg-icons'

function HeaderSocial() {
  return (
    <div className="flex items-center">
      <ul className="flex text-3xl">
        <li className="pr-5 text-orange-400"><FontAwesomeIcon icon={faLinkedin} /></li>
        <li className="pr-5 text-orange-400"><FontAwesomeIcon icon={faInstagramSquare} /></li>
        <li className="pr-5 text-orange-400"><FontAwesomeIcon icon={faFacebookSquare} /></li>
        <li className="text-orange-400"><FontAwesomeIcon icon={faTwitterSquare} /></li>
      </ul>
    </div>
  )
}

export default HeaderSocial;
