import React from 'react'
import PurpleStar from '../../Images/noun_star.png'

function HeadCarousel() {
  return (
    <div className="px-40 pb-20">
      <h1 className="w-3/6 text-4xl font-janda text-purple-500">Our Cancer-Fighting Partners</h1>
        <div className="w-3/5 relative">
          <img className="absolute w-5 right-56 -top-12" src={PurpleStar} alt="noun-star"/>
        </div>
    </div>
  )
}

export default HeadCarousel;
